.footer {
    margin-top: 68px;
    margin-bottom: 60px;
}

.footer__text {
    font-weight: 400;
    font-size: 18px;
    line-height: 1.21;
    color: #545454;
    margin: 0;
}

@media screen and (max-width: 580px) {
    .footer {
        margin-top: 50px;
        margin-bottom: 36px;
    }
    .footer__text {
        margin-left: 27px;
        font-size: 14px;
    }
}